.results {
  //@include default-page-layout();
  height: 100vh;
  width: 66vw;
  display: grid;

  margin-top: 52px;
  grid-row-gap: 44px;
}

.results-posts {
  display: grid;
  grid-row-gap: 21px;
  
  .results-posts__wrapper {
    .results-posts__posts {
      display: grid;
      grid-row-gap: 21px;
      justify-content: stretch;
    }
  }
}

.results-post {
  display: grid;
  align-content: space-around;
  grid-row-gap: 22px;

  .results-post__topics > * {
    color: $color-blue-one;
    font-size: 14px;
    line-height: 17px;
    font-weight: normal;
  }

  .results-post__title a {
    color: $color-gray-one;
    font-size: 18px;
    line-height: 22px;
  }

  .results-post__links {
   display: flex; 
  }
}

.post_link {
  min-height: 70px;
  display: flex;
  flex-basis: 205px;
  margin-right: 37px;

  .post-link__box {
    min-width: 70px;
    background-color: $color-gray-three;
    margin-right: 17px;
  }

  .post-link__link a {
    color: $color-gray-one;
    font-size: 14px;
    line-height: 17;
  }
}